import React, { useState, useEffect } from 'react';

import CryptoJS, { MD5 } from 'crypto-js';

import Pichu from './images/Pichu.svg';
import Bulbasaur from './images/Bulbasaur.svg';
import Charmander from './images/Charmander.svg';
import Squirtle from './images/Squirtle.svg';
import HappyPokemon from './images/Happy Pokemon.jpg';
import './App.css';

import Canvas from './components/Canvas';

function App() {

  const [visible, setVisible] = useState(localStorage.getItem('opened') ? true : false);
  const [userName, setUserName] = useState('');
  const [password, setPassword] = useState('');
  
  const [galleryImages, setGalleryImages] = useState([]);

  const handleEnterClick = () => {
    if (MD5(password).toString() === 'bdc87b9c894da5168059e00ebffb9077' && MD5(userName).toString() === '2c5f64ab07ccb3e410aa97fc09687cc3') {
      localStorage.setItem('opened', 'yes');
      setVisible(true);
    }
  }

  const draw = (ctx, imgSrc, width, height) => {
    ctx.drawImage(imgSrc, 0, 0, width, height);
  }

  useEffect(() => {
    if (visible) {
      setGalleryImages(
        [
          { img: Pichu, name: "Pichu", type: "svg", width: "4096", height: "4096" },
          { img: Bulbasaur, name: "Bulbasaur", type: "svg", width: "4096", height: "4096" },
          { img: Charmander, name: "Charmander", type: "svg", width: "4096", height: "4096" },
          { img: Squirtle, name: "Squirtle", type: "svg", width: "4096", height: "4096" },
          { img: HappyPokemon, name: "Happy Pokemon", type: "jpg" }
        ]
      );
    }
  }, [visible]);

  // galleryImages = [
  //   { img: Pichu, name: "Pichu", type: "svg", width: "4096", height: "4096" },
  //   { img: Bulbasaur, name: "Bulbasaur", type: "svg", width: "4096", height: "4096" },
  //   { img: Charmander, name: "Charmander", type: "svg", width: "4096", height: "4096" },
  //   { img: Squirtle, name: "Squirtle", type: "svg", width: "4096", height: "4096" },
  //   { img: HappyPokemon, name: "Happy Pokemon", type: "jpg" }
  // ];

  const downloadCanvasImage = (name, id) => {
    var link = document.createElement('a');
    link.download = name + '.png';
    link.href = document.getElementById(id).toDataURL();
    link.click();
  };

  const gallery = galleryImages.map((value, id) => {
    if (value.type === "svg") {

      const imgSrc = new Image();
      imgSrc.src = value.img;

      const canvasID = "canvas-" + value.name + "-" + id;

      return (
        <div key={value.name + "-" + id} className="image-container">
          <Canvas style={{ cursor: 'pointer' }} onClick={() => downloadCanvasImage(value.name, canvasID)} id={canvasID} draw={(ctx) => draw(ctx, imgSrc, value.width, value.height)} width={value.width} height={value.height} />
          <p className="image-text">{value.name}</p>
        </div>
      );
    } else {
      return (
        <div key={value + "-" + id} className="image-container">
          <a href={value.img} download={value.name + "." + value.type} target="_blank">
            <img className="image" src={value.img} alt={value.name} />
          </a>
          <p className="image-text">{value.name}</p>
        </div>
      );
    }
  });

  if (visible) {
    return (
      <div className="App">
        <header className="App-header">
          <h1 style={{ backgroundColor: '#FFF', marginTop: 0, padding: 10, fontSize: 64 }}>Art</h1>
        </header>
        <div className="gallery">
          {gallery}
        </div>
      </div>
    );
  } else {
    return (
      <div className="App">
        <div className="center">
          <form className="form">

            <span className="input-span">
              User name:
              <input type="text" id="name" value={userName} onChange={(e) => setUserName(e.target.value)}></input>
            </span>

            <span className="input-span">
              Password:
              <input type="password" id="password" value={password} onChange={(e) => setPassword(e.target.value)}></input>
            </span>

            <span className="input-span">
              <button onClick={() => handleEnterClick()} style={{ width: '100%' }}>Log in</button>
            </span>

          </form>
        </div>
      </div>
    )
  }
}

export default App;
